<template lang="pug">
  .page.page-manage-indep.color.white
    section.sectionHero
      .container
        .row
          .col-sm-12
            h1.title  - # {{ $route.params.id }}

        .row
          .col-sm-12
            ul.breadcrumb
              li.breadcrumb-item 
                router-link(to="/manage") 
              li.breadcrumb-item 

    section.sectionStyle
      .container
        //- pre(v-html="nowdata")
        .row
          .col-sm-4
            table.table.table-default(v-for="creator in creators")
              thead
                th 
                th 
              tbody
                tr(v-for="(d,key) in creator", v-if='translateData(d,key,translate_c) ')
                  td {{ translateData(d,key,translate_c).key }}
                  td(v-html="translateData(d,key,translate_c).data")
          .col-sm-8
            table.table.table-default
              thead
                th(style="width: 150px") 
                th 
              tbody
                tr(v-for="(d,key ) in nowdata", v-if='translateData(d,key,translate_all) ')
                  td {{ translateData(d,key,translate_all).key }}
                  td(v-html="translateData(d,key,translate_all).data")

            // el-table(:data="nowdata")
            //   el-table-column(prop="id",label="#",width=50)
            //   //- el-table-column(prop="creator_count",label="",width=40)
            //   el-table-column(prop="creators",label="",width=200)
            //   el-table-column(prop="is18",label="18",width=80)
            //   el-table-column(prop="works",label="",width=250)
            //     template(slot-scope="scope")
            //       p(v-html="scope.row.works")
            //   el-table-column(prop="created_at",label="",width=140)
            //   el-table-column(prop="updated_at",label="",width=140)
            //   el-table-column(prop="detail",label="")
            //     template(slot-scope="scope")
            //       router-link.btn.red(:to="'/manage/'+scope.row.id") 


</template>

<script>
import {mapState,mapMutations,mapActions} from 'vuex'
import $ from 'jquery'
import axios from 'axios'
export default {
  data(){
    return {
      registlist: [],
      translate_c: `id,-;
        register_id,-;
        rank,;
        age,;
        name,;
        phone,;
        mail,;
        address,;
        attend_workshop,;
        agreement_file,;
        created_at,-;
        updated_at,-;
        birthday,;
        
        `.split(";").map(o=>o.trim().split(",")),
    translate_all: `id,-;
        uuid,-;
        register_id,;
        creator_count,;
        concept_0_target,1;
        concept_0_content,1;
        concept_1_target,2;
        concept_1_content,2;
        concept_2_target,3;
        concept_2_content,3;
        work_0_name,1;
        work_0_file,1;
        work_0_content,1;
        work_1_name,2;
        work_1_file,2;
        work_1_content,2;
        work_2_name,3;
        work_2_file,3;
        work_2_content,3;
        creators,-;
        creators_all,;
        mail,;
        address,;
        attend_workshop,;
        agreement_file,;
        created_at,-;
        updated_at,-;`.split(";").map(o=>o.trim().split(","))
    }
  },
  mounted(){

    
    // axios.get('http://metro2017.test/api/regist2018/all').then(res=>{
      
    axios.get('http://api.taipeisoundscape.com/regist2018/all',{params: { token: this.token} }).then(res=>{
      console.log(res.data)
      this.registlist=res.data
    })
  },
  computed:{
    ...mapState(['token']),
    nowdata(){
      return this.use_registlist.find(o=>o.id==this.$route.params.id)
    },
    creators(){
      return (( this.nowdata && this.nowdata.creators ) || [] ).map(o=>{
        let url = "http://api.taipeisoundscape.com/registwork/file"+o.agreement_file
        let html = "<a href='"+url+"' target='_blank'></a>"
        return {
          ...o,
          agreement_file: html
        }
      })

    },
    // creators_all(){
      // return this.creators.reduce((all,creator)=>all+(all!=''?"":'')+creator.name,"")
    // },
    use_registlist(){

      let resultList= this.registlist.map(o=>({
        ...o ,
        creators_all: o.creators.reduce((all,creator)=>all+(all!=''?"":'')+creator.name,""),
        is18: (o.creators[0] && o.creators[0].age)>18,
        // works: [
        //   {
        //     id: 0,
        //     name: o.work_0_name,
        //     content: o.work_0_content,
        //     file: o.work_0_file
        //   },{
        //     id: 1,
        //     name: o.work_1_name,
        //     content: o.work_1_content,
        //     file: o.work_1_file
        //   },{
        //     id: 2,
        //     name: o.work_2_name,
        //     content: o.work_2_content,
        //     file: o.work_2_file
        //   }
          
        // ].filter(o=>o.name)
        //   .map(o=>{
        //     let url = "http://api.taipeisoundscape.com/registwork/file"+o.file
        //     let html = "<a href='"+url+"' target='_blank'>"+o.name+"</a><audio controls src='"+url+"' download></audio>"
        //     return html
        //   }).join('<br>')
      }))

      resultList.forEach(result=>{
        console.log(result)
        Object.keys(result).forEach(key=>{

          // console.log(key)
          if (key.indexOf("work")!=-1 && key.indexOf("file")!=-1  ){
              let url = "http://api.taipeisoundscape.com/registwork/file"+result[key]
              let html = "<audio controls src='"+url+"' download></audio>"
             console.log(html)
             if (result[key]!=""){
               result[key]=html

             }
          }
          if (key.indexOf("agree")!=-1 && key.indexOf("file")!=-1  ){
              let url = "http://api.taipeisoundscape.com/registwork/file"+result[key]
              let html = "<a href='"+url+"' target='_blank'></a>"
             console.log(html)
              result[key]=html
          }
        })


      })
      
      return resultList
    }
  },
  methods:{
    translateData(data,key,dict){
      let d = dict.find(o=>o[0]==key);
      if (d && d[1]){
        if (d[1]=="-"){
          return null
        }
        // console.log(d)
        if (key=="attend_workshop"){
          data = data?"":""
        }
        return {
          data,
          key: d[1]
        }
      }
      return {
        data,key
      }
    }
  }
}
</script>

<style lang="sass">
@import "../../assets/_mixins.sass"
.page-manage-list
  audio
    width: 100%
</style>



