<template lang='pug'>
.page.page-playground.color.white
  .container
    .row
      .col-sm-3
          h3.title-eng {{ $t('playground.title_eng')  }}
          h1.title {{ $t('playground.title')  }}
          p(v-html="$t('playground.description')")
          hr
          div.volume_sliders.container-fluid
            .row
              .col-2
                <!-- h4 {{volume_melody}} -->
                img.pg_icon(src="/static/img/icon_pg_melody.svg")
              .col-10
                input(type="range" v-model="volume_melody" min="0" max="100")
            .row
              .col-2
                <!-- h4 {{volume_beat}} -->
                img.pg_icon(src="/static/img/icon_pg_beats.svg")
              .col-10
                input(type="range" v-model="volume_beat" min="0" max="100")
            .row
              .col-2
                <!-- h4 {{volume_env}} -->
                img.pg_icon(src="/static/img/icon_pg_env.svg")
              .col-10
                input(type="range" v-model="volume_env" min="0" max="100")
          .row
            .btn_container.col-4(v-if='!playing')
              h5 {{ $t("playground.btn_play") }}
              .pg_btn(@click='play')
                img(src="/static/img/icon_btn_play.svg")

            .btn_container.col-4(v-if='playing')
              h5 {{ $t("playground.btn_stop") }}
              .pg_btn(@click='stop')
                img(src="/static/img/icon_btn_pause.svg")


            .btn_container.col-4
              h5 {{ $t("playground.btn_random") }}
              .pg_btn(@click="shuffle")
                img(src="/static/img/icon_btn_shuffle.svg")

            .btn_container.col-4
              h5 {{ $t("playground.btn_share") }}
              .pg_btn(@click="share_work")
                img(src="/static/img/icon_btn_share.svg")

      .col-sm-9
        .row
          //
          .col-4
            h4.col_title 
              .vc {{ $t('playground.label_melody') }}
              .reddot(:style="getball(volume_melody)")
            h3.loop_name {{ ($i18n.locale=="en")?music.melody[select.melody].name_eng:music.melody[select.melody].name_cht }}
              img.pg_icon(src="/static/img/icon_pg_melody_red.svg")
              
            select.pg_select.form-control(v-model='select.melody')
              option(v-for='(m,id) in music.melody' v-bind:value='id') {{($i18n.locale=="en")?m.name_eng:m.name_cht}} 
          .col-4
            h4.col_title 
              .vc {{ $t('playground.label_rhythm') }}
              .reddot(:style="getball(volume_beat)")
            h3.loop_name {{ ($i18n.locale=="en")?music.beat[select.beat].name_eng:music.beat[select.beat].name_cht }}
              img.pg_icon(src="/static/img/icon_pg_beats_red.svg")
              
            select.pg_select.form-control(v-model='select.beat')
              option(v-for='(m,id) in music.beat' v-bind:value='id') {{($i18n.locale=="en")?m.name_eng:m.name_cht}} 
          .col-4
            h4.col_title 
              .vc {{ $t('playground.label_sound') }}
              .reddot(:style="getball(volume_env)")
            h3.loop_name {{ ($i18n.locale=="en")?music.env[select.env].name_eng:music.env[select.env].name_cht }}
              img.pg_icon(src="/static/img/icon_pg_env_red.svg")
              
            select.pg_select.form-control(v-model='select.env')
              option(v-for='(m,id) in music.env' v-bind:value='id') {{($i18n.locale=="en")?m.name_eng:m.name_cht}} 
        .row
          .col-xs-12.d-block.d-sm-none.xs_switcher
            .row
              .col-4.xs_switch(:class="xs_part==1?'red':''" @click="xs_part=1") 
              .col-4.xs_switch(:class="xs_part==2?'red':''" @click="xs_part=2") 
              .col-4.xs_switch(:class="xs_part==3?'red':''" @click="xs_part=3") 
        
        .row
          //
          .col-sm-4.col-xs-12(v-show="!xs_mode || (xs_mode && xs_part==1)")
            ul
              li(v-for='(m,id) in music.melody' v-bind:value='id' @click='set("melody",id)' v-bind:class="(select.melody==id)?'red':''") {{($i18n.locale=="en")?m.name_eng:m.name_cht}} 
          .col-sm-4.col-xs-12(v-show="!xs_mode || (xs_mode && xs_part==2)")
            ul
              li(v-for='(m,id) in music.beat' v-bind:value='id' @click='set("beat",id)' v-bind:class="(select.beat==id)?'red':''") {{($i18n.locale=="en")?m.name_eng:m.name_cht}} 
          .col-sm-4.col-xs-12(v-show="!xs_mode || (xs_mode && xs_part==3)")
            ul
              li(v-for='(m,id) in music.env' v-bind:value='id' @click='set("env",id)' v-bind:class="(select.env==id)?'red':''") {{($i18n.locale=="en")?m.name_eng:m.name_cht}} 

        .row.pg_audio_player
          .col-sm-12
            h3 
          .col-sm-4
            audio.pg1.form-control(controls loop)
              source(:src='res_url+"1//"+music.melody[select.melody].file')
          .col-sm-4
            audio.pg2.form-control(controls loop)
              source(:src='res_url+"2//"+music.beat[select.beat].file')
          .col-sm-4
            audio.pg3.form-control(controls loop)
              source(:src='res_url+"3//"+music.env[select.env].file')
</template>

<script>
import $ from 'jquery'
  export default {
    props: ['is_in_playground'],
    data(){
      return {
        locale: "zh",
        // lang: window.data_storage.lang.page_impromptu,
        description: `101?<br><br>`,
        music: {
          melody: [
            {name_cht: "" , name_eng: "Cute" , file: ".mp3"},
            {name_cht: "" , name_eng: "Celestial" , file: ".mp3"},
            {name_cht: "" , name_eng: "Brave" , file: ".mp3"},
            {name_cht: "" , name_eng: "Witty" , file: ".mp3"},
            {name_cht: "" , name_eng: "Cheerful" , file: ".mp3"},
            {name_cht: "" , name_eng: "Optimistic" , file: ".mp3"},
            {name_cht: "" , name_eng: "Suspicious" , file: ".mp3"},

            {name_cht: "" , name_eng: "Chinese style" , file: ".mp3"},
            {name_cht: "" , name_eng: "Steadily" , file: ".mp3"},
            {name_cht: "" , name_eng: "Lively" , file: ".mp3"},
            {name_cht: "" , name_eng: "Ready to start wriggling" , file: ".mp3"},
            {name_cht: "" , name_eng: "Psychedelicly" , file: ".mp3"},
            {name_cht: "" , name_eng: "Carefully" , file: ".mp3"},
            {name_cht: "" , name_eng: "Relaxingly" , file: ".mp3"},
            {name_cht: "" , name_eng: "Lightly" , file: ".mp3"},
            // {name_cht: "" , name_eng: "Vastly" , file: ".mp3"},
            {name_cht: "" , name_eng: "Unknownly" , file: ".mp3"},
          ],
          beat: [
            {name_cht: "" , name_eng: "jogging in" , file: ".mp3"},
            {name_cht: "" , name_eng: "walking in" , file: ".mp3"},
            {name_cht: "" , name_eng: "wriggling in" , file: ".mp3"},
            {name_cht: "" , name_eng: "clapping in" , file: ".mp3"},
            {name_cht: "" , name_eng: "grooving in" , file: ".mp3"},
            {name_cht: "" , name_eng: "moving in" , file: ".mp3"},
            {name_cht: "" , name_eng: "roaming in" , file: ".mp3"},

            {name_cht: "" , name_eng: "unknownly" , file: ".mp3"},
            {name_cht: "" , name_eng: "dance at" , file: ".mp3"},
            {name_cht: "" , name_eng: "walk at" , file: ".mp3"},
            {name_cht: "" , name_eng: "stride at" , file: ".mp3"},
            {name_cht: "" , name_eng: "shake at" , file: ".mp3"},
            {name_cht: "" , name_eng: "have fun at" , file: ".mp3"},
            {name_cht: "" , name_eng: "practice at" , file: ".mp3"},
            {name_cht: "" , name_eng: "step at" , file: ".mp3"},
            {name_cht: "" , name_eng: "accelerate at" , file: ".mp3"},
            {name_cht: "" , name_eng: "stroll at" , file: ".mp3"}
          ],
          env: [
            {name_cht:"",name_eng: "Flipping through the pages of books in Sa Min Bookstore",file: ".mp3"},
            {name_cht:"",name_eng: "Playing basketball in Daan Forest Park",file: ".mp3"},
            {name_cht:"",name_eng: "Watching ducks in Daan Forest Park",file: ".mp3"},
            {name_cht:"",name_eng: "Enjoying the beautiful lake in Dahu Park",file: ".mp3"},
            {name_cht:"",name_eng: "Watching birds in Xiaobitan",file: ".mp3"},
            {name_cht:"",name_eng: "Using screws and electric drills in the construction sites of Zhongshan Station",file: ".mp3"},
            {name_cht:"",name_eng: "Students yelling Baogao before reporting to their teachers in Zongshan Junior High School",file: ".mp3"},
            {name_cht:"",name_eng: "Taking pictures in National Chiang Kai-Shek Memorial Hall",file: ".mp3"},
            {name_cht:"",name_eng: "Watching pigeons fly in National Chiang Kai-Shek Memorial Hall",file: ".mp3"},
            {name_cht:"",name_eng: "Sending letters in Taipei Beimen Post Office",file: ".mp3"},
            {name_cht:"101",name_eng: "Taipei 101 New Years Eve countdown",file: "101.mp3"},
            {name_cht:"",name_eng: "Praying before Xingtian Temple",file: ".mp3"},
            {name_cht:"",name_eng: "Getting tattoos in Ximending",file: ".mp3"},
            {name_cht:"",name_eng: "Events in Ximending",file: ".mp3"},
            {name_cht:"",name_eng: "Swiping EasyCard to get in MRT Nangang Station",file: ".mp3"},
            {name_cht:"",name_eng: "Watching movies in Miramar Cinemas",file: ".mp3"},
            {name_cht:"",name_eng: "Doing New Year Shopping in Dihua New Year",file: ".mp3"},
            {name_cht:"",name_eng: "Buying cuttlefish in Tamsui",file: ".mp3"},
            {name_cht:"",name_eng: "Playing in Taipei Expo Park",file: ".mp3"},
          
            {name_cht: "" , name_eng: "Taipei Arena selling fluorescent lights" , file: ".mp3"},
            {name_cht: "" , name_eng: "entrance of Taipei Arena" , file: ".mp3"},
            {name_cht: "" , name_eng: "Songshan Airport pulling the luggage" , file: ".mp3"},
            {name_cht: "" , name_eng: "Songshan Airport listening to the broadcast" , file: ".mp3"},
            {name_cht: "" , name_eng: "Songshan Airport watching planes" , file: ".mp3"},
            {name_cht: "" , name_eng: "Longshan Temple casting moon blocks" , file: ".mp3"},
            {name_cht: "" , name_eng: "Longshan Temple drawing lots" , file: ".mp3"},
            {name_cht: "" , name_eng: "Longshan Temple selling" , file: ".mp3"},
            {name_cht: "" , name_eng: "Dongmen station to eat Flaky scallion pancake" , file: ".mp3"},
            {name_cht: "" , name_eng: "Dongmen station to have ice cream" , file: ".mp3"},
            {name_cht: "" , name_eng: "Xiangshan steping on the fallen leaves" , file: ".mp3"},
            {name_cht: "" , name_eng: "Xiangshan going hiking and breathing" , file: ".mp3"},
            {name_cht: "" , name_eng: "Xiangshan going hiking with friends" , file: ".mp3"}
          ]
        },
        share_text: [
          " ",
          " ",
          " ",
          " ",
          " ",
          "  ",
          "  ",
          "  ",
          "  ",
          "  ",
          " ",
          "  ",
          "  ",
        ],
        select: {
          melody: 0,
          beat: 0,
          env: 0
        },
        res_url: "/static/audio//",
        status: {
          melody: false,
          beat: false,
          env: false
        },
        volume_melody: 100,
        volume_beat: 75,
        volume_env: 100,
        timer: null,
        playing: false,
        xs_part: 1,
        xs_mode: $(window).width()<768,
        sync_interval: null,

      };
    },
    
    watch:{
      select: function(){
        this.reload();
        // ga('send', 'event', 'MusicPlayground', 'Select',  this.select.melody+"," +this.select.beat+"," +this.select.env);
      },
      volume_melody: function(){
        $("audio.pg1")[0].volume=1.0*this.volume_melody/100.0;
        console.log($("audio.pg1")[0]);
      },
      volume_beat: function(){
        $("audio.pg2")[0].volume=1.0*this.volume_beat/100.0;
      },
      volume_env: function(){
        $("audio.pg3")[0].volume=1.0*this.volume_env/100.0;
      },
      is_in_playground: function(){
        if (!this.is_in_playground){
          this.stop();
        }
      }
    },
    methods: {

      sync: function(){

        $("audio.pg1")[0].currentTime
        =$("audio.pg2")[0].currentTime
        =$("audio.pg3")[0].currentTime;

        console.log("sync");
      },
      play: function(){
        // ga('send', 'event', 'MusicPlayground', 'Play', '');

        this.stop();
        this.playing=true;
        var _this=this;
        
        $("audio.pg1")[0].addEventListener('canplaythrough', function(){
          _this.status.melody=true;
          // console.log("1 ok");
        }, false);
        $("audio.pg2")[0].addEventListener('canplaythrough', function(){
          _this.status.beat=true;
          // console.log("2 ok");
        }, false);
        $("audio.pg3")[0].addEventListener('canplaythrough', function(){
          _this.status.env=true;
          // console.log("3 ok");
        }, false);
        
        this.timer=setInterval(function(){
          if (_this.status.melody && _this.status.beat && _this.status.env){
            console.log("play")
              $("audio.pg1")[0].play();
              $("audio.pg2")[0].play();
              $("audio.pg3")[0].play();
              this.playing=true;
              clearInterval(_this.timer);
              // this.sync_interval= setInterval(function(){
              //    vobj.sync();
              // },8000);

          }
        },100);

      },stop: function(){
        $("audio.pg1")[0].pause();
        $("audio.pg2")[0].pause();
        $("audio.pg3")[0].pause();
        
        $("audio.pg1")[0].currentTime=0;
        $("audio.pg2")[0].currentTime=0;
        $("audio.pg3")[0].currentTime=0;
        this.playing=false;
        // clearInterval(this.sync_interval);
      },
      shuffle: function(){
        this.select.melody= parseInt(Math.random()*this.music.melody.length);
        this.select.beat= parseInt(Math.random()*this.music.beat.length);
        this.select.env= parseInt(Math.random()*this.music.env.length);
        this.reload();
        // ga('send', 'event', 'MusicPlayground', 'Shuffle', '');
      },
      reload: function(){
        this.stop();

        this.status.melody=false;
        this.status.beat=false;
        this.status.env=false;
        
        $("audio.pg1")[0].load();
        $("audio.pg2")[0].load();
        $("audio.pg3")[0].load();
        
        this.play();
        
      },set: function(name,id){
        this.select[name]=id;
        this.reload();
      },getball: function(size){
        return {
          width: 2*Math.sqrt(size)+'px',
          height: 2*Math.sqrt(size)+'px'
        };
      },share_work: function(){
        var share_url= 'https://www.facebook.com/dialog/feed?'+
            'app_id=267788137005626'+
            '&display=popup'+
            '&title='+this.music.env[this.select.env].name_cht+''+
            '&caption= '+
            '&picture='+"http://taipeisoundscape.com/static/img/og.jpg"+
            '&description='+ this.share_text[this.select.env] +
            '&link='+"http://taipeisoundscape.com/playground?"+this.status_hash+
            '&redirect_uri=http://taipeisoundscape.com/playground?'+this.status_hash;
        window.open(share_url);
        setTimeout(()=>{
          this.play()
        },1500)
        // ga('send', 'event', 'MusicPlayground', 'Share', 'Share by FB');
      }
    },
    mounted: function(){
        // this.shuffle();
        var vobj=this;
        $("audio.pg1")[0].load();
        $("audio.pg2")[0].load();
        $("audio.pg3")[0].load();


        $(".pg_select").change(function(){
          vobj.reload();
        });
        $("audio.pg1").on("ended", vobj.sync);
        
        if (document.URL.indexOf("?")!=-1){
          let vars = decodeURI(document.URL).replace(/%3D/g,"=").split("?")[1]
          let usedata = vars.split("|").filter(o=>o).map(t=>({
            key: t.split("=")[0],
            data: t.split("=")[1]
          }))
          console.log(usedata)
          this.select.melody = parseInt(usedata.find(o=>o.key=='melody').data)
          this.select.beat= parseInt(usedata.find(o=>o.key=='beat').data)
          this.select.env= parseInt(usedata.find(o=>o.key=='env').data)
          this.volume_melody= parseInt(usedata.find(o=>o.key=='volume_melody').data)
          this.volume_beat= parseInt(usedata.find(o=>o.key=='volume_beat').data)
          this.volume_env= parseInt(usedata.find(o=>o.key=='volume_env').data)
        }
    },
    computed: {
      status_hash(){
        let list = {
          melody: this.select.melody,
          beat: this.select.beat,
          env: this.select.env,
          volume_melody: this.volume_melody,
          volume_beat: this.volume_beat,
          volume_env: this.volume_env,
        }
        return Object.keys(list).map(key=>key+"="+list[key]+"|").join("")

      }
    }
  }
</script>

<style lang="sass">
@import "../../assets/_mixins.sass"


//-------------------------------------------------------------------------
//page_playfround
.page-playground

  .bg
    position: absolute
    left: -10px
    top: 0px
    height: 100%
    +ab_center
  .bg2
    +ab_center
    position: absolute
    width: 140%
    mix-blend-mode: darken
    opacity: 0.25
  
  select
    display: none

  .btn_container
    text-align: center
    h5
      color: $colorRed
      @media screen and (min-width: $break_md)
        white-space: nowrap
  ul
    list-style: none
    padding: 0px
    margin-top: 40px
    height: 60vh
    overflow-y: scroll
    
    @media screen and (max-width: $break_sm)
      margin-top: 0px
    li
      padding: 5px
      padding-top: 10px
      padding-bottom: 10px
      padding-left: 10px
      border: solid 2px $colorRed
      position: relative
      font-weight: bold
      font-size: 14px
      cursor: pointer

      &:before,&:after
        content: ""
        display: block

        background-color: $colorRed
        +size(6px,12px)
        border-radius: 0px 6px 6px 0px
        +ab_center
      
      &:before
        left: 0
      &:after
        left: initial
        right: -2px
        border-radius: 6px 0px 0px 6px

      &.red
        background-color: $colorRed
        color: $colorWhite
        &:before,&:after
          background-color: $colorWhite
  
  .xs_switcher
    margin-top: 20px
    width: 100%
    display: flex
    margin-left: 15px
    margin-right: 15px
    .row
      width: 100%
      margin: 0

  .xs_switch
    border: solid 2px $colorRed
    cursor: pointer
    text-align: center
    background-color: transparent
    transition: 0.5s
    padding: 10px 0px
    &.red
      background-color: $colorRed
      color: $colorWhite

  .pg_audio_player
    display: none
  .pg_btn
    cursor pointer
    background-color: $colorRed
    display: inline-block
    border-radius: 50%
    position: relative
    cursor: pointer
    +size(50px,50px)
    img
      width: 30px
      +ab_center
      z-index: 5
    &:before
      display: block
      content: ""
      position: absolute
      border-radius: 50%
      +size(60px,60px)
      +ab_center
      border: solid 1px $colorRed
      z-index: 1
      transition: 0.5s

    &:hover
      // background-color: $colorWhite
      &:before
        +size(70px,70px)

  .btn_container
    display: inline-block
    margin-top: 30px
  
  h3
    max-width: 100%
    white-space: wrap
    display: inline-block

    margin-top: 15px
    .pg_icon
      display: none
    @media screen and (min-width: $break_sm)
      // white-space: nowrap
      display: inline-block


  h4.col_title
    display: flex
    align-items: center
    height: 30px
    margin-bottom: 0px
    margin-top: 30px

  .vc
    display: inline-block
  //
  .reddot
    background-color: $colorRed
    display: inline-block
    margin-left: 10px
    border-radius: 50%

  //svg icon
  .pg_icon
    width: 50px
  //slider
  .volume_sliders
    background-color: $colorRed
    color: $colorWhite
    .col-10
      padding-left: 20px
      display: flex
      justify-content: center
      align-items: center
      h3
        min-height: 2.5em
    // padding: 10px
    &.container-fluid
      padding-left: 0
      padding-right: 0
    .row
      padding: 0px 20px
    h4
      margin: 0
      font-size: 20px
    input
      width: 90%

      // position: absolute
      // top: 50%
      // width: 90%

      // +ab_center
    .col-xs-2
      padding: 0
      height: 50px
    .col-xs-10
      height: 50px


  input[type=range]
    -webkit-appearance: none /* Hides the slider so that custom slider can be made */
    width: 90% /* Specific width is required for Firefox. */
    background: transparent /* Otherwise white in Chrome */



  input[type=range]::-webkit-slider-thumb
    -webkit-appearance: none


  input[type=range]:focus
    outline: none /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */


  input[type=range]::-ms-track
    width: 100%
    cursor: pointer

    /* Hides the slider so custom styles can be added */
    background: transparent 
    border-color: transparent
    color: transparent

  input[type=range]::-webkit-slider-thumb
    -webkit-appearance: none
    border: 2px solid $colorRed
    +size(15px,15px)
    border-radius: 50%
    background: #ffffff
    cursor: pointer
    margin-top: -7.5px /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
    // box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d /* Add cool effects to your sliders! */

  input[type=range]::-webkit-slider-runnable-track
    width: 100%
    height: 3px
    cursor: pointer
    background: $colorWhite
    border-radius: 1.3px


  .col-xs-10
    position: relative
    input
      width: 100%

  .loop_name
    font-weight: 700
    
</style>


